<!--  -->
<template>
  <div class="comment">
    <h5></h5>
    <div class="comment-list">
      <ul>
        <li class="bt" v-for="(item,index) in commentList" :key="index" v-show="index < 2">
          <div class="scoredate">
            <span class="score">
              <i class="iconfont" v-if="item.score>0">&#xe626;</i>
              <i class="iconfont" v-if="item.score>1">&#xe626;</i>
              <i class="iconfont" v-if="item.score>2">&#xe626;</i>
              <i class="iconfont" v-if="item.score>3">&#xe626;</i>
              <i class="iconfont" v-if="item.score>4">&#xe626;</i>
            </span>
            <span class="date">{{item.author}}&nbsp;&nbsp;{{item.date}}</span>
          </div>
          <p
            class="comment-detail"
            :style="{'height':showmore === index ?'auto':''}"
          >{{item.content}}</p>
          <div
            class="comment-more iconfont"
            :ref="'no'+index"
            @click="showMore(index)"
            :style="{'transform':showmore === index ? 'rotate(180deg)':''}"
          >&#xe756;</div>
          <div class="pic-list" v-if="item.imgs">
			<span class="pic-count" v-show="item.imgs.length > 5">
              <span style="margin-right:.08rem" class="iconfont backicon">&#xe6dd;</span>{{item.imgs.length}}
            </span>
            <div
              class="pic"
              v-for="(pic,index) in item.imgs"
              :key="index"
              v-show="index<6"
              @click="picShow(item.imgs)"
            >
              <img :src="pic.big" alt>
            </div>
          </div>
        </li>
      </ul>
      <router-link
        :to="{'name':'AllComment'}"
        tag="div"
        class="all-comment iconfont"
      >&nbsp;&#xe63f;</router-link>
    </div>
    <gallary v-if="showPic" :swiperList="swiperList" @close="close"></gallary>
  </div>
</template>

<script>
import gallary from "./gallary";
import { debug } from "util";
export default {
  name: "comment",
  data() {
    return {
      showmore: "",
      commentList: [],
      showPic: false,
	  swiperList: [],
	   cl:[
		  '',
		  '',
		  '',
		  '?;;;--' ,
		  '',
		  '!!'
	  ]
    };
  },
  props: ["sId"],
  methods: {
    showMore(index) {
      if (this.showmore == index) {
        this.showmore = this.showmore + "u";
      } else {
        this.showmore = index;
      }
    },
    picShow(item) {
      this.swiperList = item;
      this.showPic = true;
    },
    close() {
      this.showPic = false;
    }
  },
  components: {
    gallary
  },
  mounted() {
    var keyword = "";
    switch (this.$store.state.city) {
      case "":
        keyword = "cq";
        break;
      case "":
        keyword = "bj";
        break;
      case "":
        keyword = "gl";
        break;
      case "":
        keyword = "gz";
        break;
      case "":
        keyword = "sh";
        break;
      case "":
        keyword = "tj";
        break;
      default:
        keyword = "gl";
        break;
    }
    this.$axios.get("/" + keyword + "/pl.json").then(res => {
      const data = res.data.data;
      data.commentList.map(item => {
        if (item.content.length < 130) {
             item.content +=this.cl[Math.floor(Math.random()*this.cl.length)];
        }
        if (item.imgs.length > 0) {
          item.imgs.map(i => {
            i.imgUrl = i.big;
          });
        }
        return item;
      });
	  this.commentList = data.commentList.sort(function() {
            return 0.5 - Math.random();
          });
    });
  }
};
</script>
<style lang='stylus' scoped>
.comment {
  background-color: #ffffff;
  margin-top: 0.2rem;

  h5 {
    line-height: 0.88rem;
    position: relative;
    text-indent: 0.2rem;
    border-bottom: 0.02rem solid #dadada;
    padding: 0 0.2rem;
  }

  h5::after {
    position: absolute;
    left: 0.2rem;
    top: 50%;
    height: 0.22rem;
    width: 0.04rem;
    background-color: #00bcd4;
    content: '';
    margin-top: -0.11rem;
  }

  .comment-list {
    padding: 0 0.2rem 0rem 0.2rem;

    .all-comment {
      line-height: 0.88rem;
      text-align: center;
      color: #616161;
      font-size: 0.26rem;
    }

    li {
      padding-bottom: 0.2rem;
      box-sizing: border-box;
      border-bottom: 1px dashed #e2e2e2;

      .scoredate {
        margin-top: 0.3rem;

        .score i {
          color: #ffb436;
          font-size: 0.24rem;
          transform: scale(0.8);
        }

        .date {
          float: right;
          color: #212121;
          font-size: 0.24rem;
        }
      }

      .comment-detail {
        margin-top: 0.2rem;
        height: 2.1rem;
        overflow: hidden;
        word-break: break-all;
        word-wrap: break-word;
        line-height: 0.42rem;
        font-size: 0.26rem;
        color: #616161;
      }

      .comment-more {
        text-align: center;
        line-height: 0.48rem;
        font-size: 0.5rem;
        color: #9e9e9e;
      }

      .pic-list {
        position: relative;

        .pic-count {
          background-color: rgba(0, 0, 0, 0.3);
          position: absolute;
          width: 1rem;
          height: 0.3rem;
          color: #ffffff;
          line-height: 0.3rem;
          text-align: center;
          border-radius: 0.1rem;
		  bottom .16rem;
		  right .2rem;
        }

        .pic {
          display: inline-block;
          width: 32.5%;
          height: 1.64rem;
          margin: 0 0.05rem 0.05rem 0;

          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
</style>


